import * as React from "react";
import styled from "styled-components";

export const Button = ({ variant, ...props }) => {
  if (variant === "success") {
    return <SuccessButton {...props} />;
  }

  if (variant === "danger") {
    return <DangerButton {...props} />;
  }

  return <PrimaryButton {...props} />;
};

const RawButton = styled.button`
  border: none;
  padding: ${(p) => p.theme.spaces[1]} ${(p) => p.theme.spaces[3]};
  border-radius: ${(p) => p.theme.radius[1]};
  font-size: ${(p) => p.theme.fontSizes[2]};
`;

const SuccessButton = styled(RawButton)`
  background: ${(p) => (p.inverse ? p.theme.colors.textInverse : p.theme.colors.success)};
  color: ${(p) =>
    p.inverse ? p.theme.colors.success : p.theme.colors.textInverse};

  border: ${(p) =>
    `1px solid ${p.inverse ? p.theme.colors.success : "transparent"}`};
`;

const DangerButton = styled(RawButton)`
  background: ${(p) => (p.inverse ? p.theme.colors.textInverse : p.theme.colors.danger)};
  color: ${(p) =>
    p.inverse ? p.theme.colors.danger : p.theme.colors.textInverse};

  border: ${(p) =>
    `1px solid ${p.inverse ? p.theme.colors.danger : "transparent"}`};
`;

const PrimaryButton = styled(RawButton)`
 background: ${(p) => (p.inverse ? p.theme.colors.textInverse : p.theme.colors.primary)};
  color: ${(p) =>
    p.inverse ? p.theme.colors.primary : p.theme.colors.textInverse};

  border: ${(p) =>
    `1px solid ${p.inverse ? p.theme.colors.primary : "transparent"}`};
`;
