const toPx = (x) => `${x}px`;
const toRem = (x) => `${x}rem`;

const breakpoints = {
  phablet: 361 / 16,
  tablet: 412 / 16,
  desktop: 1025 / 16,
  hd: 1441 / 16,
};

const createMediaQuery = (breakpoint) => `@media (min-width: ${breakpoint}em)`;

export const lightTheme = {
  breakpoints,
  mq: {
    mobile: createMediaQuery(breakpoints.mobile),
    phablet: createMediaQuery(breakpoints.phablet),
    tablet: createMediaQuery(breakpoints.tablet),
    desktop: createMediaQuery(breakpoints.desktop),
    hd: createMediaQuery(breakpoints.hd),
  },
  spaces: [4, 8, 12, 16, 24, 32, 48, 64, 96].map(toPx),
  fontFamilies: {
    text:
      "-apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif",
  },
  fontSizes: [0.7, 0.9, 1, 1.2, 1.3, 1.4, 1.6, 1.8, 2.2, 2.6, 3].map(toRem),
  colors: {
    primary: `hsl(231, 48%, 48%)`,
    primaryLight: `hsl(231, 48%, 97%)`,
    secondary: `rgb(233, 32, 99)`,
    text: `hsl(231, 48%, 22%)`,
    textInverse: `#fff`,
    neutral: `hsl(231, 48%, 96%)`,
    success: "#2e7d32",
    danger: "#d32f2f",
  },
  fontWeights: {
    text: 400,
    heading: 600,
    extraBold: 800,
  },
  radius: [2, 4, 8].map(toPx),
  shadows: {
    simple: `rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;`,
  },
  sizes: {
    leftMenu: "300px",
    menuMobile: "64px",
    userDropdown: "140px",
  },
  lineHeights: {
    text: 1.6,
  },
};
