import React from "react";
import { useTheme } from "../components/ThemeProvider";

const mappings = {
  padding: "spaces",
  paddingLeft: "spaces",
  paddingRight: "spaces",
  paddingBottom: "spaces",
  paddingTop: "spaces",
  margin: "spaces",
  marginTop: "spaces",
  marginLeft: "spaces",
  marginRight: "spaces",
  marginBottom: "spaces",
  background: "colors",
};

export const Box = ({ children, as = "div", ...props }) => {
  const theme = useTheme();

  const styles = Object.keys(props).reduce((acc, curr) => {
    const keyInTheme = mappings[curr];

    if (keyInTheme) {
      acc[curr] = theme[keyInTheme][props[curr]];
    } else {
      acc[curr] = props[curr];
    }

    return acc;
  }, {});

  return (
    <div css={styles} as={as}>
      {children}
    </div>
  );
};
