import * as React from "react";
import { Text } from "./Text";
import { Flex } from "./Flex";
import { Box } from "./Box";
import { Button } from "./Button";

export const DecisionTree = ({ tree, onFinish, titleAs = 'h3' }) => {
  const [node, setNode] = React.useState(tree);
  const headingRef = React.useRef();

  React.useEffect(() => {
    if (node?.result) {
      onFinish(node?.result);
    }
  }, [node, onFinish]);

  if (node?.result) {
    return null;
  }

  const handleYes = () => {
    setNode(node.yesNode);

    headingRef.current.focus();
  };

  const handleNo = () => {
    setNode(node.noNode);

    headingRef.current.focus();
  };

  const handleReset = () => {
    setNode(tree);

    headingRef.current.focus();
  };

  return (
    <div>
      <Text as={titleAs} ref={headingRef} tabIndex={-1}>
        {node.name}
      </Text>
      <Box paddingTop={2}>
        <Flex justifyContent="space-between">
          <Box>
            <Flex>
              <Box marginRight={1}>
                <Button onClick={handleYes} variant="success" inverse>
                  Yes
                </Button>
              </Box>

              <Button onClick={handleNo} variant="danger" inverse>
                No
              </Button>
            </Flex>
          </Box>
          <Button onClick={handleReset} inverse>Reset</Button>
        </Flex>
      </Box>
    </div>
  );
};
