import * as React from "react";
import styled from "styled-components";
import { AiOutlineExperiment } from "react-icons/ai";
import { Text } from "./Text";
import { Box } from "./Box";
import { ThemeProvider } from "./ThemeProvider";

const SandboxWrapper = styled.section`
  border: 1px solid ${(p) => p.theme.colors.neutral};
  border-radius: ${(p) => p.theme.radius[2]};
  margin-bottom: ${(p) => p.theme.spaces[4]};
  box-shadow: ${(p) => p.theme.shadows.simple};
  overflow: hidden;
  background: ${(p) => p.theme.colors.text};
  color: ${(p) => p.theme.colors.textInverse};

  a {
    color: ${(p) => p.theme.colors.textInverse};
    border-color: ${(p) => p.theme.colors.textInverse};
  }
`;

const SandboxContent = styled.div`
  padding: ${(p) => p.theme.spaces[3]};
`;

const SandboxTitle = styled(Text)`
  border-bottom: 1px solid ${(p) => p.theme.colors.textInverse};
  padding: ${(p) => p.theme.spaces[3]};
`;

export const Sandbox = ({ title, titleAs = "h3", ...props }) => {
  return (
    <ThemeProvider>
      <SandboxWrapper>
        <SandboxTitle as={titleAs}>
          <Box aria-hidden={true} marginRight={1} as="span">
            <AiOutlineExperiment />
          </Box>{" "}
          Sandbox - {title}
        </SandboxTitle>

        <SandboxContent {...props} />
      </SandboxWrapper>
    </ThemeProvider>
  );
};
