import * as React from "react";
import styled from "styled-components";

const SwitchButton = styled.button`
  &[role="switch"] {
    background-color: #fff;
    border: none;
    border-radius: 16px;
    position: relative;
    border: 1px solid #e6e6e6;
    transition: all 0.5s;
    transition-property: transform, background-color;
    height: 20px;
    width: 40px;
  }

  &[role="switch"] span {
    font-size: 0px;
  }

  &[role="switch"]:before {
    content: "";
    background-color: #e6e6e6;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    position: absolute;
    transition: all 0.5s;
    transition-property: transform, background-color;
    left: 1px;
    top: 1px;
  }

  &[role="switch"][aria-checked="true"] {
    background: #4cd964;
  }

  &[role="switch"][aria-checked="true"]:before {
    transform: translateX(20px);
  }
`;

export const Switch = ({
  labelledBy,
  onSwitch,
  onText = "On",
  offText = "Off",
  selected,
  ...props
}) => {
  return (
    <SwitchButton
      role="switch"
      aria-checked={selected}
      aria-labelledby={labelledBy}
      onClick={() => onSwitch(!selected)}
      {...props}
    >
      <span>{onText}</span>
      <span>{offText}</span>
    </SwitchButton>
  );
};
