import * as React from "react";
import { DecisionTree } from "../../src/components/DecisionTree";
import { Sandbox } from "../../src/components/Sandbox";
import { Text } from "../../src/components/Text";
import { Link } from "../../src/components/Link";

const CSR = "CLIENT_SIDE_RENDERING";
const STATIC_GENERATION = "STATIC_GENERATION"; // no javascript
const RUNTIME_SSR = "RUNTIME_SSR";
const BUILDTIME_SSR = "BUILDTIME_SSR";

const tree = {
  title: "My decision tree in a quiz format",
  node: {
    name: "Does the app have public pages?",
    yesNode: {
      name: "Does the app needs SEO or media sharing metas?",
      yesNode: {
        name: "Does the app needs high interactivity?",
        yesNode: {
          name: "Does the app needs instant data update / display?",
          yesNode: {
            result: RUNTIME_SSR,
          },
          noNode: {
            name: "Every user can edit public information?",
            yesNode: {
              result: RUNTIME_SSR,
            },
            noNode: {
              result: BUILDTIME_SSR,
            },
          },
        },
        noNode: {
          result: STATIC_GENERATION,
        },
      },
      noNode: {
        result: CSR,
      },
    },
    noNode: {
      result: CSR,
    },
  },
};

const ResultStrategy = ({ strategy }) => {
  const titleRef = React.useRef(null);

  React.useEffect(() => {
    if (strategy) {
      titleRef.current.focus();
    }
  }, [strategy]);

  if (strategy === CSR) {
    return (
      <div>
        <Text as="h3" ref={titleRef} tabIndex={-1}>
          <Link to="https://developers.google.com/web/updates/2019/02/rendering-on-the-web#csr">
            Client Side Rendering (CSR)
          </Link>
        </Text>

        <Text as="p">
          CSR is often used (and sometimes coupled with SSG) in order to create
          dynamic experiences such as "dashboards". If you don't have specific
          SEO requirements, this might be a good solution.
        </Text>

        <Text as="p">
          Also it's worth mentioning that the web is an amazing platform with
          tons of built-in APIs. I highly recommend checking{" "}
          <Link to="https://developer.mozilla.org/en-US/docs/Web/API">
            the MDN list of browsers APIs
          </Link>{" "}
          if you're looking for some fun.{" "}
        </Text>
      </div>
    );
  }

  if (strategy === STATIC_GENERATION) {
    return (
      <div>
        <Text as="h3" ref={titleRef} tabIndex={-1}>
          Static Site Generation with no JavaScript (SSG)
        </Text>

        <Text as="p">
          This approach is common in the documentation/blogging area where
          interactivity may not be a requirement but fast page loading is
          important. It's an approach that exists for years now thanks to tools
          like <Link to="https://github.com/jekyll/jekyll">Jekyll</Link> or{" "}
          <Link to="https://github.com/gohugoio/hugo">Hugo</Link> in other
          ecosystems. In the JavaScript one, it's worth mentioning{" "}
          <Link to="https://docusaurus.io/">Docusaurus</Link>,{" "}
          <Link to="https://github.com/11ty/eleventy/">11ty</Link>, or{" "}
          <Link to="https://github.com/vuejs/vuepress">VuePress</Link>.
        </Text>
      </div>
    );
  }

  if (strategy === RUNTIME_SSR) {
    return (
      <div>
        <Text as="h3" ref={titleRef} tabIndex={-1}>
          <Link to="https://developers.google.com/web/updates/2019/02/rendering-on-the-web#server-rendering">
            Run time Server side rendering (SSR)
          </Link>
        </Text>

        <Text as="p">
          It's a very convenient approach since it allows to render every page
          on the server. Even the ones that protected using authentication. This
          capability is not possible using other SSR strategies.
        </Text>
        <Text as="p">
          It's also worth mentioning that when building Runtime SSR by hand,
          without using dedicated tools, it can be complex to put into practice.
          Running runtime SSR at scale might also have a cost since one or
          multiple NodeJS servers have to be running to be responsive to the
          traffic. However, tools like{" "}
          <Link to="https://nextjs.org/docs/basic-features/pages#server-side-rendering">
            Nextjs
          </Link>{" "}
          have made this a breath to work with this approach.
        </Text>
      </div>
    );
  }

  if (strategy === BUILDTIME_SSR) {
    return (
      <div>
        <Text as="h3" ref={titleRef} tabIndex={-1}>
          Build time SSR (or SSG + JavaScript)
        </Text>
        <Text as="p">
          This approach mixes SSG and CSR to create scalable and interactive
          experiences.
        </Text>

        <Text as="p">
          With this approach, people from certain teams can contribute public
          pages that are very fast to load, but it does not prevent having
          richer experiences such as dashboards for authenticated users, etc.
          that SSG (strictly speaking) does not provide.
        </Text>

        <Text as="p">
          This approach is considered very scalable, highly interactive, not too
          complex to use in practice, and not expensive thanks to CDN hostings.
        </Text>

        <Text as="p">
          Tools: <Link to="https://www.gatsbyjs.com/">Gatsby</Link>,{" "}
          <Link to="https://nextjs.org/">Nextjs</Link>
        </Text>
      </div>
    );
  }

  return null;
};

const RenderingDecisionTree = () => {
  const [strategy, setStrategy] = React.useState();

  return (
    <Sandbox title={tree.title} titleAs="h2">
      <DecisionTree tree={tree.node} onFinish={setStrategy} />

      <ResultStrategy strategy={strategy} />
    </Sandbox>
  );
};

export default RenderingDecisionTree;
