import * as React from "react";
import styled from "styled-components";
import { Browser } from "../../src/components/Browser";
import { Flex } from "../../src/components/Flex";
import { Switch } from "../../src/components/Switch";
import { Label } from "../../src/components/Label";
import { Sandbox } from "../../src/components/Sandbox";
import { Box } from "../../src/components/Box";
import { VisuallyHidden } from "../../src/components/VisuallyHidden";

const RightMenu = styled.div`
  width: 30%;
  height: 100px;
`;

const RightMenuTop = styled.div`
  background: ${(p) => p.theme.colors.secondary};
  height: 88px;
  width: 100%;
`;

const RightMenuBottom = styled.div`
  background: ${(p) => p.theme.colors.primaryLight};
  height: 88px;
  margin-top: ${(p) => p.theme.spaces[4]};
`;

const AVariant = styled.p`
  background: ${(p) => p.theme.colors.textInverse};
  height: 100%;
  width: 100%;
  color: ${(p) => p.theme.colors.primary};
  padding: ${(p) => p.theme.spaces[4]};
  position: absolute;
  transition: transform 0.5s;
  transform: ${(p) => (p.hasVariant ? "rotateY(180deg)" : `rotateY(0)`)};
  border: 1px solid ${(p) => p.theme.colors.primary};
  backface-visibility: hidden;
`;

const BVariant = styled.p`
  background: ${(p) => p.theme.colors.primary};
  height: 100%;
  width: 100%;
  color: ${(p) => p.theme.colors.textInverse};
  padding: ${(p) => p.theme.spaces[4]};
  transform: rotateY(-180deg);
  position: absolute;
  transition: transform 0.5s;
  transform: ${(p) => (p.hasVariant ? "rotateY(0)" : `rotateY(-180deg)`)};
  border: 1px solid ${(p) => p.theme.colors.secondary};
  backface-visibility: hidden;
`;

const Variants = styled.div`
  margin-right: ${(p) => p.theme.spaces[4]};
  width: 100%;
  height: 200px;
  position: relative;
`;

const FeatureFlagBrowser = () => {
  const [hasVariant, setVariant] = React.useState(null);
  const liveRef = React.useRef(null);

  React.useEffect(() => {
    if (hasVariant === null) return;
    liveRef.current.textContent = `You've switched to the ${
      hasVariant ? "new homepage" : "old homepage"
    } variant.`;
    
  }, [hasVariant]);

  return (
    <Sandbox title="Runtime Feature flags">
      <Browser url="https://mfrachet-test-ab.com/">
        <Flex direction="row">
          <Variants>
            <BVariant hasVariant={hasVariant} aria-hidden={!hasVariant}>
              The new amazing homepage!
            </BVariant>
            <AVariant hasVariant={hasVariant} aria-hidden={hasVariant}>The old meeh homepage</AVariant>
          </Variants>

          <RightMenu>
            <RightMenuTop />
            <RightMenuBottom />
          </RightMenu>
        </Flex>
      </Browser>

      <Box marginTop={4}>
        <Flex justifyContent="space-between">
          <Label id="switch-flag">Switch the "new homepage" feature flag</Label>

          <Switch
            labelledBy="switch-flag"
            selected={hasVariant}
            onSwitch={setVariant}
          />
        </Flex>
      </Box>

      <VisuallyHidden>
        <p aria-live="polite" ref={liveRef}></p>
      </VisuallyHidden>
    </Sandbox>
  );
};

export default FeatureFlagBrowser;
