import React from "react";
import styled from "styled-components";

export const Text = React.forwardRef(({ as, ...props }, ref) => {
  if (as === "h1") {
    return <H1 {...props} ref={ref} />;
  }

  if (as === "h2") {
    return <H2 {...props} ref={ref} />;
  }

  if (as === "h3") {
    return <H3 {...props} ref={ref} />;
  }

  if (as === "h4") {
    return <H4 {...props} ref={ref} />;
  }

  if (as === "h5") {
    return <H5 {...props} ref={ref} />;
  }

  if (as === "h6") {
    return <H6 {...props} ref={ref} />;
  }

  if (as === "span") {
    return <Span {...props} ref={ref} />;
  }

  if (as === "p") {
    return <P {...props} ref={ref} />;
  }

  return null;
});

// outline: none!important exists to override the * {} rule for managing keyboard / mouse focus
const H1 = styled.h1`
  font-size: ${(p) => p.theme.fontSizes[10]};
  font-weight: ${(p) => p.theme.fontWeights.extraBold};
  line-height: 1.4;
  margin-bottom: ${(p) => p.theme.spaces[4]};
  background-image: linear-gradient(
    90deg,
    ${(p) => p.theme.colors.primary},
    ${(p) => p.theme.colors.secondary}
  );
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-background-clip: text;
  -moz-text-fill-color: transparent;
  outline: none!important;
`;
const H2 = styled.h2`
  font-size: ${(p) => p.theme.fontSizes[7]};
  line-height: 1.4;
  margin-bottom: ${(p) => p.theme.spaces[1]};
  margin-top: ${(p) => p.theme.spaces[2]};
  outline: none!important;
`;
const H3 = styled.h3`
  font-size: ${(p) => p.theme.fontSizes[6]};
  line-height: 1.4;
  margin-bottom: ${(p) => p.theme.spaces[1]};
  margin-top: ${(p) => p.theme.spaces[2]};
  outline: none!important;
`;
const H4 = styled.h4`
  font-size: ${(p) => p.theme.fontSizes[5]};
  line-height: 1.4;
  margin-bottom: ${(p) => p.theme.spaces[1]};
  margin-top: ${(p) => p.theme.spaces[2]};
  outline: none!important;
`;
const H5 = styled.h5`
  font-size: ${(p) => p.theme.fontSizes[4]};
  line-height: 1.4;
  margin-bottom: ${(p) => p.theme.spaces[1]};
  margin-top: ${(p) => p.theme.spaces[2]};
  outline: none!important;
`;
const H6 = styled.h6`
  font-size: ${(p) => p.theme.fontSizes[3]};
  line-height: 1.4;
  margin-bottom: ${(p) => p.theme.spaces[1]};
  margin-top: ${(p) => p.theme.spaces[2]};
  outline: none!important;
`;
const P = styled.p`
  font-size: ${(p) => p.theme.fontSizes[2]};
  line-height: 1.8;
  margin-bottom: ${(p) => p.theme.spaces[3]};
  outline: none!important;
`;
const Span = styled.span`
  font-size: ${(p) => p.theme.fontSizes[2]};
  line-height: 1.8;
  outline: none!important;
`;
