import * as React from "react";
import styled from "styled-components";

const BrowserContainer = styled.div`
  /* The browser window */
  .container {
    border: 3px solid #f1f1f1;
    background: ${(p) => p.theme.colors.textInverse};
    border-radius: ${(p) => p.theme.radius[1]};
  }

  /* Container for columns and the top "toolbar" */
  .row {
    padding: 10px;
    background: #f1f1f1;
  }

  /* Create three unequal columns that floats next to each other */
  .column {
    float: left;
  }

  .left {
    width: 15%;
  }

  .right {
    width: 10%;
  }

  .middle {
    width: 75%;
  }

  /* Clear floats after the columns */
  .row:after {
    content: "";
    display: table;
    clear: both;
  }

  /* Three dots */
  .dot {
    margin-top: 4px;
    height: 12px;
    width: 12px;
    background-color: #bbb;
    border-radius: 50%;
    display: inline-block;
  }

  /* Style the input field */
  input[type="text"] {
    width: 100%;
    border-radius: 3px;
    border: none;
    background-color: white;
    margin-top: -8px;
    height: 25px;
    color: #666;
    padding: 5px;
  }

  /* Three bars (hamburger menu) */
  .bar {
    width: 17px;
    height: 3px;
    background-color: #aaa;
    margin: 3px 0;
    display: block;
  }

  /* Page content */
  .content {
    padding: ${(p) => p.theme.spaces[4]};
    position: relative;
  }
`;

export const Browser = ({ url, children }) => {
  return (
    <BrowserContainer>
      <div className="container">
        <div className="row">
          <div className="column left">
            <span className="dot" style={{ background: "#ED594A" }}></span>
            <span className="dot" style={{ background: "#FDD800" }}></span>
            <span className="dot" style={{ background: "#5AC05A" }}></span>
          </div>
          <div className="column middle">
            <input
              type="text"
              value={`http://${url}`}
              aria-hidden={true}
              disabled={true}
            />
          </div>
          <div className="column right">
            <div style={{ float: "right" }}>
              <span className="bar"></span>
              <span className="bar"></span>
              <span className="bar"></span>
            </div>
          </div>
        </div>

        <div className="content">{children}</div>
      </div>
    </BrowserContainer>
  );
};
